tliche Stelle bei uns zu kontaktieren! ")]),_c('p',[_vm._v(" Wenn Sie glauben, dass die Verarbeitung Ihrer Daten gegen das Datenschutzrecht verstößt oder Ihre datenschutzrechtlichen Ansprüche in sonst einer Weise verletzt worden sind, können Sie sich bei der Aufsichtsbehörde beschweren. Diese ist für Österreich die Datenschutzbehörde, deren Website Sie unter "),_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://www.dsb.gv.at/?tid=121879084","target":"_blank","rel":"noopener"}},[_vm._v("https://www.dsb.gv.at/")]),_vm._v(" finden. In Deutschland gibt es für jedes Bundesland einen Datenschutzbeauftragten. Für nähere Informationen können Sie sich an die "),_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://www.bfdi.bund.de","target":"_blank","rel":"noopener"}},[_vm._v("Bundesbeauftragte für den Datenschutz und die Informationsfreiheit (BfDI)")]),_vm._v(" wenden. Für unser Unternehmen ist die folgende lokale Datenschutzbehörde zuständig: ")]),_c('h2',{staticClass:"adsimple-121879084",attrs:{"id":"google-fonts-datenschutzerklaerung"}},[_vm._v(" Google Fonts Datenschutzerklärung ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v(" Warum verwenden wir Google Fonts auf unserer Website? ")]),_c('p',[_vm._v(" Mit Google Fonts können wir auf der eigenen Webseite Schriften nutzen, und müssen sie nicht auf unserem eigenen Server hochladen. Google Fonts ist ein wichtiger Baustein, um die Qualität unserer Webseite hoch zu halten. Alle Google-Schriften sind automatisch für das Web optimiert und dies spart Datenvolumen und ist speziell für die Verwendung bei mobilen Endgeräten ein großer Vorteil. Wenn Sie unsere Seite besuchen, sorgt die niedrige Dateigröße für eine schnelle Ladezeit. Des Weiteren sind Google Fonts sichere Web Fonts. Unterschiedliche Bildsynthese-Systeme (Rendering) in verschiedenen Browsern, Betriebssystemen und mobilen Endgeräten können zu Fehlern führen. Solche Fehler können teilweise Texte bzw. ganze Webseiten optisch verzerren. Dank des schnellen Content Delivery Network (CDN) gibt es mit Google Fonts keine plattformübergreifenden Probleme. Google Fonts unterstützt alle gängigen Browser (Google Chrome, Mozilla Firefox, Apple Safari, Opera) und funktioniert zuverlässig auf den meisten modernen mobilen Betriebssystemen, einschließlich Android 2.2+ und iOS 4.2+ (iPhone, iPad, iPod). Wir verwenden die Google Fonts also, damit wir unser gesamtes Online-Service so schön und einheitlich wie möglich darstellen können. ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v(" Welche Daten werden von Google gespeichert? ")]),_c('p',[_vm._v(" Wenn Sie unsere Webseite besuchen, werden die Schriften über einen Google-Server nachgeladen. Durch diesen externen Aufruf werden Daten an die Google-Server übermittelt. So erkennt Google auch, dass Sie bzw. Ihre IP-Adresse unsere Webseite besucht. Die Google Fonts API wurde entwickelt, um Verwendung, Speicherung und Erfassung von Endnutzerdaten auf das zu reduzieren, was für eine ordentliche Bereitstellung von Schriften nötig ist. API steht übrigens für „Application Programming Interface“ und dient unter anderem als Datenübermittler im Softwarebereich. ")]),_c('p',[_vm._v(" Google Fonts speichert CSS- und Schrift-Anfragen sicher bei Google und ist somit geschützt. Durch die gesammelten Nutzungszahlen kann Google feststellen, wie gut die einzelnen Schriften ankommen. Die Ergebnisse veröffentlicht Google auf internen Analyseseiten, wie beispielsweise Google Analytics. Zudem verwendet Google auch Daten des eigenen Web-Crawlers, um festzustellen, welche Webseiten Google-Schriften verwenden. Diese Daten werden in der BigQuery-Datenbank von Google Fonts veröffentlicht. Unternehmer und Entwickler nützen das Google-Webservice BigQuery, um große Datenmengen untersuchen und bewegen zu können. ")]),_c('p',[_vm._v(" Zu bedenken gilt allerdings noch, dass durch jede Google Font Anfrage auch Informationen wie Spracheinstellungen, IP-Adresse, Version des Browsers, Bildschirmauflösung des Browsers und Name des Browsers automatisch an die Google-Server übertragen werden. Ob diese Daten auch gespeichert werden, ist nicht klar feststellbar bzw. wird von Google nicht eindeutig kommuniziert. ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v(" Wie lange und wo werden die Daten gespeichert? ")]),_c('p',[_vm._v(" Anfragen für CSS-Assets speichert Google einen Tag lang auf seinen Servern, die hauptsächlich außerhalb der EU angesiedelt sind. Das ermöglicht uns, mithilfe eines Google-Stylesheets die Schriftarten zu nutzen. Ein Stylesheet ist eine Formatvorlage, über die man einfach und schnell z.B. das Design bzw. die Schriftart einer Webseite ändern kann. ")]),_c('p',[_vm._v(" Die Font-Dateien werden bei Google ein Jahr gespeichert. Google verfolgt damit das Ziel, die Ladezeit von Webseiten grundsätzlich zu verbessern. Wenn Millionen von Webseiten auf die gleichen Schriften verweisen, werden sie nach dem ersten Besuch zwischengespeichert und erscheinen sofort auf allen anderen später besuchten Webseiten wieder. Manchmal aktualisiert Google Schriftdateien, um die Dateigröße zu reduzieren, die Abdeckung von Sprache zu erhöhen und das Design zu verbessern. ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v(" Wie kann ich meine Daten löschen bzw. die Datenspeicherung verhindern? ")]),_c('p',[_vm._v(" Jene Daten, die Google für einen Tag bzw. ein Jahr speichert können nicht einfach gelöscht werden. Die Daten werden beim Seitenaufruf automatisch an Google übermittelt. Um diese Daten vorzeitig löschen zu können, müssen Sie den Google-Support auf "),_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://support.google.com/?hl=de&tid=121879084"}},[_vm._v("https://support.google.com/?hl=de&tid=121879084")]),_vm._v(" kontaktieren. Datenspeicherung verhindern Sie in diesem Fall nur, wenn Sie unsere Seite nicht besuchen. ")]),_c('p',[_vm._v(" Anders als andere Web-Schriften erlaubt uns Google uneingeschränkten Zugriff auf alle Schriftarten. Wir können also unlimitiert auf ein Meer an Schriftarten zugreifen und so das Optimum für unsere Webseite rausholen. Mehr zu Google Fonts und weiteren Fragen finden Sie auf "),_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://developers.google.com/fonts/faq?tid=121879084"}},[_vm._v("https://developers.google.com/fonts/faq?tid=121879084")]),_vm._v(". Dort geht zwar Google auf datenschutzrelevante Angelegenheiten ein, doch wirklich detaillierte Informationen über Datenspeicherung sind nicht enthalten. Es ist relativ schwierig, von Google wirklich präzise Informationen über gespeicherten Daten zu bekommen. ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v("Rechtsgrundlage")]),_c('p',[_vm._v(" Wenn Sie eingewilligt haben, dass Google Fonts eingesetzt werden darf, ist die Rechtsgrundlage der entsprechenden Datenverarbeitung diese Einwilligung. Diese Einwilligung stellt laut"),_c('strong',{staticClass:"adsimple-121879084"},[_vm._v(" Art. 6 Abs. 1 lit. a DSGVO (Einwilligung)")]),_vm._v(" die Rechtsgrundlage für die Verarbeitung personenbezogener Daten, wie sie bei der Erfassung durch Google Fonts vorkommen kann, dar. ")]),_c('p',[_vm._v(" Von unserer Seite besteht zudem ein berechtigtes Interesse, Google Font zu verwenden, um unser Online-Service zu optimieren. Die dafür entsprechende Rechtsgrundlage ist "),_c('strong',{staticClass:"adsimple-121879084"},[_vm._v("Art. 6 Abs. 1 lit. f DSGVO (Berechtigte Interessen)")]),_vm._v(". Wir setzen Google Font gleichwohl nur ein, soweit Sie eine Einwilligung erteilt haben. ")]),_c('p',[_vm._v(" Google verarbeitet Daten von Ihnen u.a. auch in den USA. Wir weisen darauf hin, dass nach Meinung des Europäischen Gerichtshofs derzeit kein angemessenes Schutzniveau für den Datentransfer in die USA besteht. Dies kann mit verschiedenen Risiken für die Rechtmäßigkeit und Sicherheit der Datenverarbeitung einhergehen. ")]),_c('p',[_vm._v(" Als Grundlage der Datenverarbeitung bei Empfängern mit Sitz in Drittstaaten (außerhalb der Europäischen Union, Island, Liechtenstein, Norwegen, also insbesondere in den USA) oder einer Datenweitergabe dorthin verwendet Google sogenannte Standardvertragsklauseln (= Art. 46. Abs. 2 und 3 DSGVO). Standardvertragsklauseln (Standard Contractual Clauses – SCC) sind von der EU-Kommission bereitgestellte Mustervorlagen und sollen sicherstellen, dass Ihre Daten auch dann den europäischen Datenschutzstandards entsprechen, wenn diese in Drittländer (wie beispielsweise in die USA) überliefert und dort gespeichert werden. Durch diese Klauseln verpflichtet sich Google, bei der Verarbeitung Ihrer relevanten Daten, das europäische Datenschutzniveau einzuhalten, selbst wenn die Daten in den USA gespeichert, verarbeitet und verwaltet werden. Diese Klauseln basieren auf einem Durchführungsbeschluss der EU-Kommission. Sie finden den Beschluss und die entsprechenden Standardvertragsklauseln u.a. hier: "),_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://eur-lex.europa.eu/eli/dec_impl/2021/914/oj?locale=de","target":"_blank","rel":"noopener"}},[_vm._v("https://eur-lex.europa.eu/eli/dec_impl/2021/914/oj?locale=de")])]),_c('p',[_vm._v(" Die Google Ads Datenverarbeitungsbedingungen (Google Ads Data Processing Terms), welche auch den Standardvertragsklauseln für Google Fonts entsprechen, finden Sie unter "),_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://business.safety.google/adsprocessorterms/","target":"_blank","rel":"noopener"}},[_vm._v("https://business.safety.google/adsprocessorterms/")]),_vm._v(". ")]),_c('p',[_vm._v(" Welche Daten grundsätzlich von Google erfasst werden und wofür diese Daten verwendet werden, können Sie auch auf "),_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://policies.google.com/privacy?hl=de&tid=121879084"}},[_vm._v("https://www.google.com/intl/de/policies/privacy/")]),_vm._v(" nachlesen. ")]),_c('h2',{staticClass:"adsimple-121879084",attrs:{"id":"google-maps-datenschutzerklaerung"}},[_vm._v(" Google Maps Datenschutzerklärung ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v(" Warum verwenden wir Google Maps auf unserer Website? ")]),_c('p',[_vm._v(" All unsere Bemühungen auf dieser Seite verfolgen das Ziel, Ihnen eine nützliche und sinnvolle Zeit auf unserer Webseite zu bieten. Durch die Einbindung von Google Maps können wir Ihnen die wichtigsten Informationen zu diversen Standorten liefern. Sie sehen auf einen Blick wo wir unseren Firmensitz haben. Die Wegbeschreibung zeigt Ihnen immer den besten bzw. schnellsten Weg zu uns. Sie können den Anfahrtsweg für Routen mit dem Auto, mit öffentlichen Verkehrsmitteln, zu Fuß oder mit dem Fahrrad abrufen. Für uns ist die Bereitstellung von Google Maps Teil unseres Kundenservice. ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v(" Welche Daten werden von Google Maps gespeichert? ")]),_c('p',[_vm._v(" Damit Google Maps ihren Dienst vollständig anbieten kann, muss das Unternehmen Daten von Ihnen aufnehmen und speichern. Dazu zählen unter anderem die eingegebenen Suchbegriffe, Ihre IP-Adresse und auch die Breiten- bzw. Längenkoordinaten. Benutzen Sie die Routenplaner-Funktion wird auch die eingegebene Startadresse gespeichert. Diese Datenspeicherung passiert allerdings auf den Webseiten von Google Maps. Wir können Sie darüber nur informieren, aber keinen Einfluss nehmen. Da wir Google Maps in unsere Webseite eingebunden haben, setzt Google mindestens ein Cookie (Name: NID) in Ihrem Browser. Dieses Cookie speichert Daten über Ihr Userverhalten. Google nutzt diese Daten in erster Linie, um eigene Dienste zu optimieren und individuelle, personalisierte Werbung für Sie bereitzustellen. ")]),_c('p',[_vm._v(" Folgendes Cookie wird aufgrund der Einbindung von Google Maps in Ihrem Browser gesetzt: ")]),_c('p',[_c('strong',{staticClass:"adsimple-121879084"},[_vm._v("Name:")]),_vm._v(" NID"),_c('br'),_c('strong',{staticClass:"adsimple-121879084"},[_vm._v("Wert:")]),_vm._v(" 188=h26c1Ktha7fCQTx8rXgLyATyITJ121879084-5"),_c('br'),_c('strong',{staticClass:"adsimple-121879084"},[_vm._v("Verwendungszweck:")]),_vm._v(" NID wird von Google verwendet, um Werbeanzeigen an Ihre Google-Suche anzupassen. Mit Hilfe des Cookies „erinnert“ sich Google an Ihre am häufigsten eingegebenen Suchanfragen oder Ihre frühere Interaktion mit Anzeigen. So bekommen Sie immer maßgeschneiderte Werbeanzeigen. Das Cookie enthält eine einzigartige ID, die Google benutzt, um Ihre persönlichen Einstellungen für Werbezwecke zu sammeln."),_c('br'),_c('strong',{staticClass:"adsimple-121879084"},[_vm._v("Ablaufdatum:")]),_vm._v(" nach 6 Monaten ")]),_c('p',[_c('strong',{staticClass:"adsimple-121879084"},[_vm._v("Anmerkung:")]),_vm._v(" Wir können bei den Angaben der gespeicherten Daten keine Vollständigkeit gewährleisten. Speziell bei der Verwendung von Cookies sind Veränderungen nie auszuschließen. Um das Cookie NID zu identifizieren, wurde eine eigene Testseite angelegt, wo ausschließlich Google Maps eingebunden war. ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v(" Wie lange und wo werden die Daten gespeichert? ")]),_c('p',[_vm._v(" Die Google-Server stehen in Rechenzentren auf der ganzen Welt. Die meisten Server befinden sich allerdings in Amerika. Aus diesem Grund werden Ihre Daten auch vermehrt in den USA gespeichert. Hier können Sie genau nachlesen wo sich die Google-Rechenzentren befinden: "),_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://www.google.com/about/datacenters/inside/locations/?hl=de","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("https://www.google.com/about/datacenters/inside/locations/?hl=de")])]),_c('p',[_vm._v(" Die Daten verteilt Google auf verschiedenen Datenträgern. Dadurch sind die Daten schneller abrufbar und werden vor etwaigen Manipulationsversuchen besser geschützt. Jedes Rechenzentrum hat auch spezielle Notfallprogramme. Wenn es zum Beispiel Probleme bei der Google-Hardware gibt oder eine Naturkatastrophe die Server lahm legt, bleiben die Daten ziemlich sicher trotzdem geschützt. ")]),_c('p',[_vm._v(" Manche Daten speichert Google für einen festgelegten Zeitraum. Bei anderen Daten bietet Google lediglich die Möglichkeit, diese manuell zu löschen. Weiters anonymisiert das Unternehmen auch Informationen (wie zum Beispiel Werbedaten) in Serverprotokollen, indem es einen Teil der IP-Adresse und Cookie-Informationen nach 9 bzw.18 Monaten löscht. ")]),_c('h3',{staticClass:"adsimple-121879084"},[_vm._v(" Wie kann ich meine Daten löschen bzw. die Datenspeicherung verhindern? ")]),_c('p',[_vm._v(" Mit der 2019 eingeführten automatischen Löschfunktion von Standort- und Aktivitätsdaten werden Informationen zur Standortbestimmung und Web-/App-Aktivität – abhängig von Ihrer Entscheidung – entweder 3 oder 18 Monate gespeichert und dann gelöscht. Zudem kann man diese Daten über das Google-Konto auch jederzeit manuell aus dem Verlauf löschen. Wenn Sie Ihre Standorterfassung vollständig verhindern wollen, müssen Sie im Google-Konto die Rubrik „Web- und App-Aktivität“ pausieren. Klicken Sie „Daten und Personalisierung“ und dann auf die Option „Aktivitätseinstellung“. Hier können Sie die Aktivitäten ein- oder ausschalten. ")]),_c('p',[_vm._v(" In Ihrem Browser können Sie weiters auch einzelne Cookies deaktivieren, löschen oder verwalten. Je nach dem welchen Browser Sie verwenden, funktioniert dies immer etwas anders. Die folgenden Anleitungen zeigen, wie Sie Cookies in Ihrem Browser verwalten: ")]),_c('p',[_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://support.google.com/chrome/answer/95647?tid=121879084","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Chrome: Cookies in Chrome löschen, aktivieren und verwalten")])]),_c('p',[_c('a',{staticClass:"adsimple-121879084",attrs:{"href":"https://support.apple.com/de-at/guide/safari/sfri11471/mac?tid=121879084","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Safari: Verwalten von Cookies und Websitedaten mit Safari")])]),_c('p',[_c('a',{staticClass:"adsimple-121